.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 32px;
  overflow: hidden;
  @media screen and(min-width: 320px) {
    max-width: 100%;
    padding: 0 20px;
  }
  @media screen and(min-width: 480px) {
    max-width: 480px;
    padding: 0 32px;
  }
  @media screen and(min-width:768px) {
    max-width: 768px;
  }
  @media screen and(min-width:1280px) {
    max-width: 1280px;
  }
}
